        'Aunque utiliza un apellido común (Hernández), la combinación con "Mario" y el icono del unicornio dorado la hace altamente distintiva. El simbolismo del unicornio añade fantasía y lujo, diferenciando la marca en el segmento de marroquinería premium.',
